import { Link } from "gatsby";
import React, { useState } from "react";
import styled from "styled-components";
import { menuData } from "../../data/menuData";
import MenuButton from "../buttons/MenuButton";
import MenuTooltip from "../tooltips/MenuTooltip";

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);
  const screenType = typeof window !== `undefined` ? require("react-screentype-hook").default() : { isMobile: false };

  function handleClick(event) {
    setIsOpen(!isOpen);
    event.preventDefault();
  }

  return (
    <Wrapper>
      <Link to="/">
        <img src="/images/logos/logo.png" width="55px" />
      </Link>
      <MenuWrapper count={!screenType.isMobile ? menuData.length : 1}>
        { !screenType.isMobile ? (
          menuData.slice(0, menuData.length - 1).map((item, index) => (
            <a href={item.link}>
              <MenuButton item={item} key={index} />
            </a>
          ))
        ) : (
          <MenuButton
            item={menuData[menuData.length - 1]}
            key={menuData.length}
            onClick={(event) => handleClick(event)}
          />
        )}
      </MenuWrapper>
      <MenuTooltip isOpen={isOpen} />
    </Wrapper>
  );
}

const Wrapper = styled.div`
  position: absolute;
  top: 60px;
  display: grid;
  grid-template-columns: 44px auto;
  width: 100%;
  justify-content: space-between;
  padding: 0 50px;
  align-items: center;
`;

const MenuWrapper = styled.div`
  display: grid;
  gap: 35px;
  grid-template-columns: repeat(${(props) => props.count}, auto);
`;
